.pageNumbers {
    /*min-width: 250px;*/
    margin-top: 1em;
    width: 100%;
    display: flex;
    cursor: pointer;
    justify-content: center;
}

.pageNumbers span {
    text-align: center;
    width: 25px;
    height: 20px;
    margin: 2px;
    padding: 2px;
    border: 1px solid;
}

.pageNumbers button {
    margin-left: 2em;
    margin-right: 2em;
}

.selectedPage {
    font-size: 18px;
    font-weight: bold;
}

.paginatorBox {
    min-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}